"use client";

import { useEffect, useRef } from "react";
import { motion, useMotionValue, useTransform } from "framer-motion";

export default function RobotFace() {
  const containerRef = useRef(null);

  // Persistent motion values
  const mouseX = useMotionValue(0);
  const mouseY = useMotionValue(0);

  // Translate motion values
  const translateX = useMotionValue(0);
  const translateY = useMotionValue(0);

  // Mouse move handler
  useEffect(() => {
    const container = containerRef.current;
    if (!container) return;

    const handleMouseMove = (e) => {
      const rect = container.getBoundingClientRect();
      const x = e.clientX - rect.left - rect.width / 2;
      const y = e.clientY - rect.top - rect.height / 2;

      mouseX.set(x);
      mouseY.set(y);
      translateX.set(x);
      translateY.set(y);
    };

    container.addEventListener("mousemove", handleMouseMove);
    return () => container.removeEventListener("mousemove", handleMouseMove);
  }, []);

  // Rotate & translate mappings

  // ✅ Normal rotation & translation
  const rotateXNormal = useTransform(mouseY, [-100, 100], [20, -20]);
  const rotateYNormal = useTransform(mouseX, [-100, 100], [-20, 20]);
  const xTranslateNormal = useTransform(translateX, [-100, 100], [-5, 5]);
  const yTranslateNormal = useTransform(translateY, [-100, 100], [5, -5]);

  // 🔁 Inverted rotation & translation
  const rotateXInverted = useTransform(mouseY, [-100, 100], [-15, 15]);
  const rotateYInverted = useTransform(mouseX, [-100, 100], [40, -40]);
  const xTranslateInverted = useTransform(translateX, [-100, 100], [7, -7]);
  const yTranslateInverted = useTransform(translateY, [-100, 100], [-10, 10]);

  return (
    <div
      ref={containerRef}
      className="relative w-[100vw]   h-[100vh] flex justify-center bg-transparent"
    >

      <motion.img
        src="/images/robot/43.png"
        alt="Robot Normal"
        className="w-60 h-60 absolute top-[35] left-[520] "
        style={{
          rotateX: rotateXNormal,
          rotateY: rotateYNormal,
          
          transformPerspective: 800,
        }}
      />


      <motion.img
        src="/images/robot/34.png"
        alt="Robot Inverted"
        className=" w-60 h-60 absolute top-[35] right-[605]"
        style={{
          rotateX: rotateXNormal,
          rotateY: rotateYNormal,
          x: xTranslateNormal ,
          y: yTranslateInverted ,
          transformPerspective: 800,
        }}
      />

       {/* <img className=' w-[400px] h-[500px]' src="/images/ij.jpg " alt="" /> */}
    </div>
  );
}
